* {
  box-sizing: border-box;
}
ul {
  list-style: none;
}
h2 {
  font-size: 40px;
  margin: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-item {
  line-height: 1;
  font-size: 24px;
  font-weight: 500;
}

.wrap {
  display: flex;
  align-items: center;
  gap: 10px;
}
